<template>
  <div>
    <el-card>
      <!-- -->
      <el-row>
        <el-col :span="8">
          <el-input placeholder="" v-model="queryInfo.query" clearable>
          </el-input>
        </el-col>
      </el-row>
      <!-- -->
      <el-table :data="ordersList" stripe border>
        <el-table-column type="index"></el-table-column>
        <el-table-column label="" prop="order_number"></el-table-column>
        <el-table-column label="" prop="order_price" width="120"></el-table-column>
        <el-table-column label="" prop="order_pay" width="100">
          <template v-slot="scope">
            <el-tag type="success" v-if="scope.row.order_pay === '1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" prop="is_send" width="70"></el-table-column>
        <el-table-column label="" prop="create_time">
          <template v-slot="scope">
            {{ scope.row.create_time | dateFormat }}
          </template>
        </el-table-column>
        <el-table-column label="" width="130">
          <el-button type="primary" icon="el-icon-edit" size="mini"
                     @click="editDialogVisible = true"></el-button>
          <el-button type="success" icon="el-icon-location" size="mini"
                     @click=" showProcessDialog"></el-button>
        </el-table-column>
      </el-table>
      <!-- -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[5,7, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
    <!---->
    <el-dialog
      title=""
      :visible.sync="editDialogVisible"
      width="50%"
      :modal-append-to-body="false"
      @close="handleCloseDialog"
    >
      <el-form :model="addressFrom" :rules="addressFromRules" ref="addressFromRef" label-width="70px">
        <el-form-item label="/:" prop="address1" label-width="100px">
          <el-cascader
            v-model="addressFrom.address1"
            :options="cityData"
            :props="{ expandTrigger: 'hover' }"></el-cascader>
        </el-form-item>
        <el-form-item label=":" prop="address2" label-width="100px">
          <el-input v-model="addressFrom.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
         <el-button @click="editDialogVisible = false"> </el-button>
         <el-button type="primary" @click="handleEdit"> </el-button>
      </span>
    </el-dialog>
    <!---->
    <el-dialog
      title=""
      :visible.sync="processDialogVisible"
      width="50%"
      :modal-append-to-body="false"
    >
      <el-timeline>
        <el-timeline-item
          v-for="(activity, index) in processData"
          :key="index"
          :timestamp="activity.time">
          {{ activity.context }}
        </el-timeline-item>
      </el-timeline>
    </el-dialog>
  </div>
</template>

<script>
import cityData from '@/common/citydata'
import kuaiDi from '@/common/kuaidi'

export default {
  name: 'OrdersList',
  data () {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },
      ordersList: [],
      total: 0,
      editDialogVisible: false,
      addressFrom: {
        address1: [],
        address2: ''
      },
      addressFromRules: {
        address1: {
          required: true,
          message: '/',
          trigger: 'blur'
        },
        address2: {
          required: true,
          message: '',
          trigger: 'blur'
        }
      },
      cityData,
      processDialogVisible: false,
      processData: []
    }
  },
  methods: {
    async fetchOrdersList () {
      const { data: res } = await this.$http.get('orders', { params: this.queryInfo })
      if (res.meta.status !== 200) { return this.$message.error(`${res.meta.msg}`) }
      this.ordersList = res.data.goods
      this.total = res.data.total
    },
    handleSizeChange (newSize) {
      this.queryInfo.pagesize = newSize
    },
    handleCurrentChange (newPage) {
      this.queryInfo.pagenum = newPage
    },
    handleEdit () {
      if (this.addressFrom.address1.length !== 0 && this.addressFrom.address2.length !== 0) {
        this.$message.success('')
        this.editDialogVisible = false
      } else {
        this.$message.error('')
      }
    },
    handleCloseDialog () {
      this.$refs.addressFromRef.resetFields()
    },
    showProcessDialog () {
      this.processData = kuaiDi.data
      this.processDialogVisible = true
    }
  },
  created () {
    this.fetchOrdersList()
  },
  watch: {
    queryInfo: {
      deep: true,
      handler () {
        this.fetchOrdersList()
      }
    }
  }
}
</script>

